.App {
  text-align: center;
}

.card-columns {
  display: flex !important;
  justify-content: space-around;
}

.rightCard {
  margin-left: 15px;
  border: none !important
}

.strTags {
  background-color: turquoise;
  padding: 2px;
  width: 50%;
  margin: auto !important;
  margin-top: 5px !important;
}

.youtube {
  width: 100% !important;
}

.mealName {
  text-shadow: 0 13.36px 8.896px #1e4b75, 0 -2px 1px rgb(242, 248, 214) !important;
  font-weight: bold !important;
  color: black;
  letter-spacing: 1px;
  background-color: white !important;
}

.mealName:hover {
  color: white;
  text-shadow: 0 13.36px 8.896px #242323, 0 -2px 1px rgb(141, 55, 55) !important;
  background-color: black !important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.footer {
  background-color: rgb(45, 51, 51);
  color: white;
  padding: 15px;
}

.navbar {
  background-color: rgb(64, 65, 65) !important;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.page-enter {
  opacity: 0.01;
  transform: translateX(-100%);
}

.page-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: all 300ms ease-in;
}

.page-exit {
  opacity: 1;
  transform: translateX(0%);
}

.page-exit-active {
  opacity: 0.01;
  transform: translateX(100%);
  transition: all 300ms ease-out;
}

.category {
  color: rgb(206, 198, 198) !important;
  background-color: rgb(19, 28, 82) !important;
  padding: 15px;
}

.category:hover {
  text-decoration: none !important;
}